
.bgContainer{
    background-image: url("../../assets/bg.jpg");
    background-size: cover;
    background-position: center;
    height: 100vh;
 

}




.choiceBoxes{
    display: flex;
    justify-content: center;
}

.linkBox{
    text-decoration: none;
}

@keyframes fadeIn {
    0%{
        opacity:0;
    }

    100%{
        opacity:1;
    }
}

.check-wrapper{
    display:block;
    justify-content: center;
    animation: fadeIn 2s ease-in;
    animation-delay: 2s;
    opacity: 0;
    animation-fill-mode: forwards;
}
